::-webkit-scrollbar {
    width: 12px;
}
 
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
    border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}
/*IE*/
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
.scrollable {
        scrollbar-base-color: rgba(128, 128, 128, 1);
        scrollbar-face-color: #666666;
        scrollbar-3dlight-color: #666666;
        scrollbar-highlight-color: #666666;
        scrollbar-track-color: rgba(117, 117, 117, 0.79);
        /*scrollbar-arrow-color: rgba(117, 117, 117, 0.79);*/
        scrollbar-arrow-color: rgba(51, 51, 51, 1);
        scrollbar-shadow-color: #666666;
        scrollbar-dark-shadow-color: #666666;
  }
  .scrollable-in {
        scrollbar-face-color:  rgba(87, 87, 87, 1);
        scrollbar-track-color: rgba(51, 51, 51, 1);
        scrollbar-arrow-color: rgba(128, 128, 128, 1);
        scrollbar-shadow-color:  rgba(87, 87, 87, 1);
  }
}

/*Edge*/
@supports (-ms-ime-align:auto)  {
.scrollable {
        margin-right: -10px;
        padding-top: 16px;
        margin-top: -16px;
        margin-bottom: -16px;
        padding-bottom: 16px;
}
  
.scrollable:after {
    content: "";
    height: 16px;
    display: block;
}
}




