	max-width: 100%;
    overflow: hidden;
    padding-bottom:2%;
}
.AcordeonItem{
	transition: all 0.25s ease;
    overflow: hidden;
    max-height: 1.7em;
    transition: max-height 0.25s ease-out;
    overflow: hidden;
    cursor: pointer;
}
.AcordeonItem h4{
    font-size: 1em;
    padding-left: 0.5em;
